// import { letterStr } from '@/constants/globalDatas';
import { ProviderContext } from '@/reducer/context';
import getUrlParams from '@/utils/get-url-params';
import { Button, Carousel, Icon } from 'antd-mobile';
import React, { useContext } from 'react';
import { useHistory } from 'react-router-dom';
import {
  Page,
  HeaderBox,
  GrayFont,
  FontContent,
  CarouselItem,
  CarouseBox,
  ButtonBox,
  ButtonBoxInner,
  ParsingFont,
  ResolveBox,
  ResolveItem,
  ResolveLabel,
  ResolveResultItem,
  ResolveResultList,
  ResolveTitle,
  HeaderBlank,
  HeaderInner,
  ButtonBlank,
} from '../../styles';
import getMathpix from '../../utils/getMathpix';

const Parsing: React.FC = () => {
  const history = useHistory();
  const { tag } = getUrlParams();

  const { state } = useContext(ProviderContext);
  const { topic = [], topicSelectAnswer = {} } = state;

  return (
    <Page>
      <HeaderBox>
        <HeaderBlank />
        <HeaderInner>
          <Icon
            type="cross"
            color="#262626"
            onClick={(): void => history.go(-3)}
          />
        </HeaderInner>
      </HeaderBox>
      <CarouseBox>
        <Carousel dots={false} style={{ height: '100%' }}>
          {topic?.map((topicItem, index) => {
            const { questionId, question, checkAnswers, parse } = topicItem;
            // const letterTemp = letterStr.split(''); // 遍历解答label
            // const letterTrueTemp = letterStr.split(''); // 遍历正确答案label
            const prevNum = index + 1;
            const prevSelect = topicSelectAnswer[prevNum];
            return (
              <CarouselItem key={questionId + tag}>
                <HeaderBox className="lr-pd">
                  <GrayFont>
                    {questionId} {tag}
                  </GrayFont>
                  <GrayFont>
                    {index + 1}/{topic.length}
                  </GrayFont>
                </HeaderBox>
                <FontContent>{getMathpix(question)}</FontContent>
                <ResolveBox>
                  <ResolveTitle>解答</ResolveTitle>
                  {checkAnswers?.map((val) => {
                    // 获取答案label
                    // const itemLabel = letterTemp
                    //   .splice(0, item.length)
                    //   .join('');
                    // item 中可能有多种答案，但是其中index是相同的
                    // index的取值可以直接取第一个
                    const item = val[0];
                    const itemArr = item.index?.split('');
                    return (
                      <ResolveItem key={item.index}>
                        <ResolveLabel>{item.index}</ResolveLabel>
                        <ResolveResultList>
                          {itemArr?.map((item2, index2) => {
                            // itemArr遍历为了确定判断第几个选项答案是否正确
                            let isTrue = false;
                            val.forEach((item3) => {
                              isTrue =
                                prevSelect?.[item.index]?.split('')[index2] ===
                                item3.answer.split('')[index2];
                            });
                            return (
                              <ResolveResultItem
                                className={isTrue ? '' : 'error'}
                              />
                            );
                          })}
                        </ResolveResultList>
                      </ResolveItem>
                    );
                  })}
                </ResolveBox>
                <ParsingFont>
                  <div className="title">正确答案</div>
                  {checkAnswers?.map((val) => {
                    const item = val[0];

                    // 获取答案label
                    // const itemLabel = letterTrueTemp
                    //   .splice(0, item.length)
                    //   .join('');
                    return (
                      <ResolveItem key={item.index}>
                        <ResolveLabel>{item.index}</ResolveLabel>
                        <ResolveResultList>
                          {val?.map((item2) => item2.answer).join('、')}
                        </ResolveResultList>
                      </ResolveItem>
                    );
                  })}
                  <br />
                  解析：
                  <br />
                  {getMathpix(parse)}
                </ParsingFont>
              </CarouselItem>
            );
          })}
        </Carousel>
      </CarouseBox>
      <ButtonBox>
        <ButtonBlank />
        <ButtonBoxInner>
          <Button type="primary" onClick={(): void => history.go(-1)}>
            返回报告
          </Button>
        </ButtonBoxInner>
      </ButtonBox>
    </Page>
  );
};

export default Parsing;
